/* 全局样式 */
body {
  font-family: sans-serif;
  margin: 20px;
}

/* 棋盘布局 */
.board-row {
  display: flex;
  justify-content: center;
}

.square {
  width: 60px;
  height: 60px;
  margin: 2px;
  font-size: 24px;
  font-weight: bold;
  border: 1px solid #999;
  background: #fff;
  cursor: pointer;
}

.square:hover {
  background: #f0f0f0;
}

.square.winner {
  background: #4CAF50;
  /* 获胜格子高亮 */
}

/* 游戏状态和布局 */
.status {
  margin: 10px 0;
  font-size: 18px;
}

.game {
  display: flex;
  gap: 20px;
}

.game-info ol {
  padding-left: 20px;
}